import * as HTTP from './http';

export const agencyLogin = async(user, pass) => {
  let endpoint = `agencyLogin.php`;

  let urlParams = `?user=${user}&pass=${pass}`

  let data = HTTP.GET(`${endpoint}${urlParams}`);

  return data;
}

