///////////////////////////////////POST///////////////////////////////////

export const agencyNewAgent = async (cid, name, pass, mail, level) => {
  let endpoint = `agencyNewAgent.php`;

  const postData = {}
  postData['cid'] = cid;
  postData['name'] = name;
  postData['mail'] = mail;
  postData['pass'] = pass;
  postData['level'] = level;
  
  let data = HTTP.POST(endpoint, postData);

  return data;
}

